'use client';

import React, { Suspense } from 'react';
import { useSearchParams } from 'next/navigation';
import { AuthLayout } from '@sunny/ui/auth/AuthLayout';
import { SignInForm } from '@sunny/ui/auth/SignInForm';
import { SocialAuthButtons } from '@sunny/ui/auth/SocialAuthButtons';
import { useAuthRouter } from '@sunny/auth/AuthRouter';
import { useAuth } from '@sunny/auth/hooks';
import { UserAccountType, type SignInRequest, type SocialAuthRequest } from '@sunny/shared-types/auth';

function SignInPageContent() {
  const searchParams = useSearchParams();
  const { handleAuthSuccess, getDetectedAccountType } = useAuthRouter();
  const { signIn, socialAuth } = useAuth();

  const redirectUrl = searchParams?.get('redirect') || undefined;
  const accountType = searchParams?.get('type') as UserAccountType || getDetectedAccountType();

  const handleSignIn = async (data: SignInRequest) => {
    const response = await signIn(data);
    
    if (response.success && response.user) {
      handleAuthSuccess(response.user);
    }
    
    return response;
  };

  const handleSocialAuth = async (data: SocialAuthRequest) => {
    const response = await socialAuth(data);
    
    if (response.success && response.user) {
      handleAuthSuccess(response.user);
    }
    
    return response;
  };

  return (
    <AuthLayout
      title="Welcome Back"
      subtitle="Sign in to your Sunny Payments account"
      backgroundVariant="gradient"
    >
      <div className="space-y-6">
        <SignInForm
          onSubmit={handleSignIn}
          defaultAccountType={accountType}
          redirectUrl={redirectUrl}
        />
        
        <SocialAuthButtons
          onSocialAuth={handleSocialAuth}
          accountType={accountType}
          mode="signin"
        />
      </div>
    </AuthLayout>
  );
}

export default function SignInPage() {
  return (
    <Suspense fallback={
      <AuthLayout
        title="Welcome Back"
        subtitle="Loading..."
        backgroundVariant="gradient"
      >
        <div className="flex items-center justify-center py-12">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-green-600"></div>
        </div>
      </AuthLayout>
    }>
      <SignInPageContent />
    </Suspense>
  );
}
